import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@mui/material';
import Chip from '@mui/material/Chip';
import Stack  from '@mui/material/Stack';
import Link from '@mui/material/Link';
import { deviceList, firmVersions, deviceFirmwareUpdate } from '../api';
// const devices = [
//   { id: 1, name: 'RZ G2L ID:100', currentFirmware: '1.0.0' },
//   { id: 2, name: 'RZ G2L ID:101', currentFirmware: '1.2.0' },
//   // ... more devices
// ];


const firmwareVersions = ['1.0.0', '1.2.0', '1.5.0', '2.0.0'];

const Visualizer = () => {
  const [selectedFirmware, setSelectedFirmware] = useState('');
  const [selectedDevice, setSelectedDevice] = useState('');
  const [firmwareVersions, setfirmwareVersions] = useState([]);
  const [devices, setdevices] = useState([])


 

  useEffect(()=>{
  const loader = () => {
  deviceList().then((data) => {
    setdevices(data)
  })
 }
 loader();
 console.log("SetDevices", selectedFirmware)
}, [])

  const handleFirmwareChange = (event, deviceId) => {
    // Logic to update firmware selection for the specific device (deviceId)
    const value = event.target.value;
    // setfirmwareVersions()
    console.log(`Selected firmware for device ${deviceId}:`, event.target.value);
    setSelectedFirmware(prev => ({
      ...prev,
      [deviceId]: value
    }));
  };

  const handleUpdate = async (deviceId) => {
    // Simulate OTA update process for the specific device (deviceId)
    const {data} = await deviceFirmwareUpdate(deviceId, selectedFirmware)
    console.log("Dtaa", data)
    alert(`Updating device ${deviceId} to firmware version ${selectedFirmware}...`);
  };

  return (
    <Box>
      <Typography variant="h5" gutterBottom>
        OTA Update
      </Typography>
      <Grid container spacing={2}>
        {devices.map((device) => (
          <Grid item xs={12} sm={6} md={4} key={device.id} >
            <Card>
              <CardContent>
                <Box display= "flex" alignItems="center" flexWrap="nowrap" gap={2}>
                <Typography variant="h6">{device.name} </Typography> <Stack direction="row" spacing={1}>

      {device.isActive? <Chip label="Active" variant="outlined"  sx={{backgroundColor:'#4caf50'}}/> :
       <Chip label="InActive" variant="outlined"  sx={{backgroundColor:'red'}}/>}

    </Stack>
    </Box>
    <Link href="/changlog" underline="hover" sx={{mt: 10, fontSize: '1.2rem', fontWeight: 'bold', color: '#4caf50',
      visibility: device.hasUpdate ? 'visible': 'hidden',
    animation: 'blink 1s step-start 0s infinite',
    // '@keyframes blink': {
    //   '0%': { opacity: 1},
    //   '50%': { opacity: 0},
    //   '100%': {opacity:1}
    // },
    // '&:hover':{
    //   animation: 'none',
    //   opacity: 1
    // }
    }}>Latest Firmware  Availabe</Link>
  

                <Typography variant="body2" sx={{fontSize: '1.0rem'}}>
                  Current Firmware: {device.currentFirmware}
                </Typography>
                <FormControl fullWidth margin="normal">
                  <InputLabel id={`firmware-select-label-${device.id}`}>
                    Select Firmware
                  </InputLabel>
                  <Select
                    label={`firmware-select-label-${device.id}`}
                    id={`firmware-select-${device.id}`}
                    value={selectedFirmware[device.id] } // You might need to manage selectedFirmware per device
                    onChange={(event) => handleFirmwareChange(event, device.id)}
                  >
                    {device.firmwareVersions.map((version) => (
                      <MenuItem key={version} value={version}>
                        {version}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => handleUpdate(device.name)}
                  disabled={!selectedFirmware[device.id] || device.currentFirmware === selectedFirmware[device.id]} // Enable only if firmware is selected
                >
                  Update
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default Visualizer;
